.previouspostslink {
  width: 2.25rem;
  height: 2.25rem;
  border: 1px solid #408F95;
  margin-right: 1.75rem;
  position: relative;
  transition: all 0.2s;
}

.nextpostslink {
  width: 2.25rem;
  height: 2.25rem;
  border: 1px solid #408F95;
  margin-left: 1.75rem;
  position: relative;
  transition: all 0.2s;
}

.pagination__prev {
  width: 2.25rem;
  height: 2.25rem;
  border: 1px solid #408F95;
  margin-right: 1.75rem;
  position: relative;
  transition: all 0.2s;
}

.pagination__prev::after {
  content: "";
  display: inline-block;
  width: 12px;
  height: 12px;
  border-left: 1px solid #408F95;
  border-bottom: 1px solid #408F95;
  position: absolute;
  top: 50%;
  left: 60%;
  transform: translate(-50%, -50%) rotate(45deg);
  transition: all 0.2s;
}

.pagination__prev:hover::after {
  border-left: 1px solid #fff;
  border-bottom: 1px solid #fff;
}

.pagination__prev:hover {
  background-color: #408F95;
}

.pagination__next {
  width: 2.25rem;
  height: 2.25rem;
  border: 1px solid #408F95;
  margin-left: 1.75rem;
  position: relative;
  transition: all 0.2s;
}

.pagination__next::after {
  content: "";
  width: 12px;
  height: 12px;
  border-top: 1px solid #408F95;
  border-right: 1px solid #408F95;
  position: absolute;
  top: 50%;
  left: 40%;
  transform: translate(-50%, -50%) rotate(45deg);
  transition: all 0.2s;
}

